<template>
  <div class="container">
    <div class="home-top">
      <div class="info">
        <div class="name">我是undefined</div>
        <div class="desc">很高兴遇见你~</div>
      </div>
    </div>
    <div class="list">
      <List :isShow="isShowList" />
    </div>
  </div>
</template>

<script>
import List from "./components/list";
export default {
  mounted() {
    window.addEventListener("scroll", () => {
      let top =
        document.documentElement.scrollTop ||
        document.body.scrollTop ||
        window.pageYOffset;
      if (top >= 40) {
        this.isShowList = true;
      } else if (top <= 20) {
        this.isShowList = false;
      }
    });
  },
  data() {
    return {
      isShowList: false,
    };
  },
  methods: {},
  // watch:{},
  components: {
    List,
  },
};
</script>

